import Vue from 'vue'
import Vuex from 'vuex'
import banamex from "./bancos/banamex"
import scotiabank from "./bancos/scotiabank"
import hsbc from "./bancos/hsbc"
import banorte from "./bancos/banorte"
import santander from "./bancos/santander"
import banregio from "./bancos/banregio"
import bxmas from "./bancos/bxmas"
import afirme from "./bancos/afirme"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {

    productoSeleccionado: "Comprar mi Casa",

    // tipo de ingreso

    tiposDeIngreso: [
      "Asalariado",
      "Profesionista Independiente",
      "Independiente",
      "Socio de Empresa",
      "Negocio Propio"
    ],
    tipoDeIngresoSeleccionado: "Asalariado",

    // apoyos

    tipoDeApoyoSeleccionado: "",

    montoDeApoyo: 0,

    // ingreso mensual

    ingresoMensual: 0,

    // valor del inmueble

    valorDelInmueble: 0,

    // gastos mensuales

    gastosMensuales: 0,

    // tipo de pago

    tipoDePago: "",

    // plazo

    plazoSeleccionado: 2,

    // monto de credito

    montoDeCredito: 0,

    modal: 0,


  },
  getters: {

    banamex(state) {
      return banamex(state)
    },

    scotiabank(state) {
      return scotiabank(state)
    },

    hsbc(state) {
      return hsbc(state)
    },

    banorte(state) {
      return banorte(state)
    },

    santander(state) {
      return santander(state)
    },

    banregio(state) {
      return banregio(state)
    },

    bxmas(state) {
      return bxmas(state)
    },

    afirme(state) {
      return afirme(state)
    },

    creditoPreAutorizado(state) {
      if (state.tipoDeIngresoSeleccionado == "Asalariado") {
        return (state.ingresoMensual / 2) * 100
      } else {
        return (state.ingresoMensual * .36) * 100
      }
    },

    _montoDeCredito(state) {
      if (state.tipoDeApoyoSeleccionado == "cofinavit") {

        if (state.montoDeApoyo >= (state.valorDelInmueble * .15)) {
          return state.montoDeCredito || (state.valorDelInmueble * 1.1) - state.montoDeApoyo;
        } else if (state.montoDeApoyo < (state.valorDelInmueble * .15) && state.montoDeApoyo > (state.valorDelInmueble * .05)) {
          return state.montoDeCredito || state.valorDelInmueble - state.montoDeApoyo
        } else if (state.montoDeApoyo <= (state.valorDelInmueble * .05)) {
          return state.montoDeCredito || state.valorDelInmueble * .95;
        }

      } else if (state.tipoDeApoyoSeleccionado == "infonavit") {

        return state.montoDeCredito || state.valorDelInmueble * .95;

      } else {



        return state.montoDeCredito;
      }
    },
    _montoDeCreditoObj(state) {

    },
    montoDeCreditoConEscrituras(state) {
      if (state.montoDeApoyo >= (state.valorDelInmueble * .15)) {
        return true
      } else {
        return false
      }
    },
    banamex(state) {
      return banamex(state)
    }
  },
  mutations: {
    // tipo de ingreso
    updateTipoDeIngresoSeleccionado(state, tipoDeIngresoSeleccionado) {
      state.tipoDeIngresoSeleccionado = tipoDeIngresoSeleccionado;
    },
    // tipo de apoyo
    updateTipoDeApoyoSeleccionado(state, tipoDeApoyoSeleccionado) {
      state.tipoDeApoyoSeleccionado = tipoDeApoyoSeleccionado;
    },
    updateMontoDeApoyo(state, montoDeApoyo) {
      state.montoDeApoyo = montoDeApoyo;
    },
    // ingreso mensual
    updateIngresoMensual(state, ingresoMensual) {
      state.ingresoMensual = ingresoMensual;
    },
    // valor del inmueble
    updateValorDelInmueble(state, valorDelInmueble) {
      state.valorDelInmueble = valorDelInmueble;
    },
    // gastos mensuales
    updateGastosMensuales(state, gastosMensuales) {
      state.gastosMensuales = gastosMensuales;
    },
    // tipo de pago
    updateTipoDePago(state, tipoDePago) {
      state.tipoDePago = tipoDePago;
    },
    // plazoSeleccionado
    updatePlazoSeleccionado(state, plazoSeleccionado) {
      state.plazoSeleccionado = plazoSeleccionado;
    },
    // monto de credito
    updateMontoDeCredito(state, montoDeCredito) {
      state.montoDeCredito = montoDeCredito;
    },
    // modal
    updateModal(state, modal) {
      state.modal = modal;
    },
  },
  actions: {

  }
})